@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.infobox{
    -webkit-flex: 1 1;
            flex: 1 1;
    cursor: pointer;
}

.infobox:not(:last-child){
    margin-right: 15px;
}

.infobox__cases{
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infobox__total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}

.infobox--selected {
    border-top: 10px solid black;
}

.infobox__cases--red{
    border-color: red !important;
    color: red;

}

.infobox__cases--green{
    color: lightgreen !important;
    border-color:lightgreen;
}

.infobox__cases--grey{
    color: grey !important;
    border-color:grey;
}

.infobox__cases--blue{
    color: blue;
    border-color:blue;
}


.map{
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container{
    height: 100%;
}

.info-name{
    font-size: 20px;
    font-weight: bold;
    color: #555;
}

.info-flag img{
    width: 100px;
    border-radius: 5px;
}

.info-flag{
    height: 100px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-container{
    width: 150px;
}

.info-confirmed,
.info-recovered,
.info-deaths,
.info-vaccinated {
    font-size: 10px;
    margin-top: 5px;
}
.table {
    margin-top : 20px;
    overflow:scroll;
    height : 400px;
    color: #6a5d5d;
    background-color: white;
}

.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}

.table tr{
    display : -webkit-flex;
    display : flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.table td{
    padding: 0.5rem;
}
* {
  margin:0;
}

body{
  background-color:#dcdce0;
}

.app{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-evenly;
          justify-content:space-evenly;
  padding:20px;
}

.app__header {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  margin-bottom: 20px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.app__header > h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app__dropdown{
  background-color: white;
}

.app__left{
  -webkit-flex: 0.9 1;
          flex: 0.9 1;
}

.app__right{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.app__right .MuiCardContent-root{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow:1;
          flex-grow:1;
}

.app__graph{
  -webkit-flex-grow:1;
          flex-grow:1;
}

.app__graphTitle{
  margin-top: 20px;
  margin-bottom: 20px;
}

.app__stats{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-between;
          justify-content:space-between;
}

@media(max-width : 990px){
  .app{
    -webkit-flex-direction:column;
            flex-direction:column;
  }
}
